import React, { Component } from 'react';
import {
  Link,
  Redirect
} from 'react-router-dom';
import { connect } from 'react-redux';
import Input from '../../components/input';
import Button from '../../components/button';
import { register } from 'actions/user';

@connect(
  state => state.user,
  { register }
)
class Register extends Component {
  state = {
    user: '',
    pwd: '',
    repeatpwd: ''
  };

  handleChange = (e, type) => {
    this.setState({ [type]: e.target.value.trim() });
  }

  handleRegister = () => {
    this.props.register(this.state);
  }

  render() {
    return (
      <div className="register-container">
        {
          (this.props.redirectTo && this.props.redirectTo !== '/user/register') ?
            <Redirect to={this.props.redirectTo} /> : null
        }
        <div className="form">
          <Input
            labelText=""
            inputName="username"
            inputType="text"
            handleChange={(e) => this.handleChange(e, 'user')}
            value={this.state.user}
          />
          <Input
            labelText=""
            inputName="password"
            inputType="password"
            handleChange={(e) => this.handleChange(e, 'pwd')}
            value={this.state.pwd}
          />
          <Input
            labelText=""
            inputName="confirmPsw"
            inputType="password"
            handleChange={(e) => this.handleChange(e, 'repeatpwd')}
            value={this.state.repeatpwd}
          />
          <div className="bottom">
            <div className="btn-wrapper">
              <Button type="primary" onClick={this.handleRegister}></Button>
            </div>
            <div className="login">
              <Link to="/user/login"></Link>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Register;




